<template>
    <div>
        <display style="background-color: #E9EEF3;">
            <template slot="main" >
                <div>
                    <el-table
                    :data="tableData"
                    height="400"
                    style="width: 100%">
                    <el-table-column fixed prop="id" label="编号" width="240"></el-table-column>
                    <el-table-column prop="name" label="姓名" width="240"></el-table-column>
                    <el-table-column prop="status" label="是否能连续发布消息" width="240">
                        <template slot-scope="scope">
                            <el-switch
                            style="display: block"
                            v-model="scope.row.status"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            @change="change(scope.row)">
                            </el-switch>
                        </template>
                    </el-table-column>
                </el-table>
                 <div class="block">
                    <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                    </el-pagination>
                </div>
               </div>
            </template>
        </display>
    </div>
</template>
<script>
    import display from './common/display'
    import baseUrl from '../../config/config'
    import axios from 'axios'
    import Vue from 'Vue'
    export default {
        name :'Home',
        components : {
            "display":display
        },
        data(){
            return {
                authority_list:[],
                tableData:[],
                pageSize:10,
                currentPage: 1,
                total:new Number,
            }
        },
        methods :{
            handleClick(row) {
                var _this = this
                var openid = row.open_id
                axios.put(baseUrl.basePath+'/oper/audit_update',{
                    openid:openid,
                    status:row.status
                }).then((res) =>{
                    if(res.data.code == "200"){
                        _this.$message({
                            showClose:true,
                            message:'操作成功！',
                            type:'success'
                        })
                    }else{
                        _this.$message({
                            showClose:true,
                            message:'操作失败！',
                            type:'error'
                        })
                    }
                }).catch((err) =>{
                })
            },
             handleSizeChange(vals) {
                var _this = this
                _this.pageSize = vals
                axios.get(baseUrl.basePath+'/prize/prize_list',{
                    params :{
                        pageNum:1,
                        pageSize:vals,
                    }
                }).then(function(res){
                    _this.tableData = res.data.result1,
                    _this.total = res.data.result
                       // res.data.result1[0].imgUrl == '1'
                }).catch(function(error){
                })
            },
            handleCurrentChange(val) {
                var _this = this
                axios.get(baseUrl.basePath+'/home/prize_list',{
                    params :{
                        pageNum:`${val}`,
                        pageSize:_this.pageSize,
                    }
                }).then(function(res){
                    _this.tableData = res.data.result1,
                    _this.total = res.data.result
                }).catch(function(error){
                })
            },
            change(row){
                var _this = this
                axios.put(baseUrl.basePath+'/oper/authority',{
                    value:row
                }).then((res) =>{
                    if(res.data.msg == '更新成功'){
                        _this.$message({
                            showClose:true,
                            message:'操作成功！',
                            type:'success'
                        })
                    }else{
                        _this.$message({
                            showClose:true,
                            message:'操作失败！',
                            type:'error'
                        })
                    }
                }).then((err) =>{
                    console.log(err)
                })
            }
        },
        mounted() {
            var that = this
            axios.get(baseUrl.basePath+'/oper/authority_list')
            .then((res) =>{
                that.tableData = res.data
                console.log(res)
            }).catch((err) =>{
                console.log(err)
            })
        },     
    }

</script>
<style>
    .block{
        padding: 10px 0 0 0;
        float: left;
    }  
</style>




