import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import prize_list from '@/components/prize_list'
import prize_edit from '@/components/prize_edit'
import prize_add from '@/components/prize_add'
import member from '@/components/member'
import draw_view_employee from '@/components/draw_view_employee'
import annua_member from '@/components/annual_member'
import draw_view_family from '@/components/draw_view_family'
import draw_list_edit from '@/components/draw_list_edit'
import draw_list_f_edit from '@/components/draw_list_f_edit'
import audit from '@/components/audit'
import authority from '@/components/authority'
Vue.use(Router)


const router = new Router({
    routes : [
        {path:'/',component:login},
        {path:'/prize_list',component:prize_list,meta:{requireAuth:true}},
        {path:'/prize_edit',component:prize_edit,meta:{requireAuth:true}},
        {path:'/prize_add',component:prize_add,meta:{requireAuth:true}},
        {path:'/member_list',component:member,meta:{requireAuth:true}},
        {path:'/draw_list/employee',component:draw_view_employee,meta:{requireAuth:true}},
        {path:'/draw_list/edit_e',component:draw_list_edit,meta:{requireAuth:true}},
        {path:'/draw_list/edit_f',component:draw_list_f_edit,meta:{requireAuth:true}},
        {path:'/draw_list/family',component:draw_view_family,meta:{requireAuth:true}},
        {path:'/annual_member_list',name:'annual_member',component:annua_member,meta:{requireAuth:true}},
        {path:'/audit',component:audit,meta:{requireAuth:true}},
        {path:'/authority',component:authority,meta:{requireAuth:true}}
    ],
})
router.beforeEach((to,from,next) =>{
    if(to.matched.some(res => res.meta.requireAuth)){
        if(localStorage.getItem('username')){
            next()
        }else{
            next({
                path:'/prize_list',
                query:{redirect:to.fullPath}
            })
        }
        // var timer = setInterval(() => {
        //     localStorage.clear('username');
        //     if(!localStorage.getItem('username')){
        //         clearInterval(timer)
        //     }
        // },1000*60*10)
    }else{
        next()
    }
})
export default router


